	module.exports = {"data":{"markdownRemark":{"html":"<h2>The shift</h2>\n<p>In the past few years a shift seems to be happening from jQuery to other libraries/frameworks like React, Angular, Vue and many more. As great as these tools are they might be overkill for many smaller and maybe middle-sized projects.</p>\n<h2>From jQuery to Native API's</h2>\n<p>jQuery has received much scorn from the most of the developers in the community in the past few years., but is it justified? If we look back on the past jQuery has been a great tool. It large support in browsers in the time when nothing was standardized. It made DOM selection and CSS selection really easy and had a convenient animation API. And some more pro's like a huge selection of plugins etc.</p>\n<p>However nowadays browser suppliers are standardizing, their API's to some degree which we can leverage from. This made the advantage of normalizing the old browser API's obsolete.</p>\n<p><strong>Examples</strong></p>\n<p>Want to get your element with id?</p>\n<pre><code>    // jQuery\n    $('#my-id')\n\n    // Native API\n    document.querySelector('#my-id')\n</code></pre>\n<p>Maybe you want to get all of the elements with a class</p>\n<pre><code>    // jQuery\n    $('.my-class')\n\n    // Native API\n    document.querySelectorAll('.my-class')\n</code></pre>\n<p>Easy right? Okay, this might not be the hardest task so let's try another one.</p>\n<p><strong>Lazy loading images</strong>\nSo how about lazy loading? For the jQuery example, we will be using the plugin called <em>Lazy load</em></p>\n<pre><code>    // jQuery\n\n    window.addEventListener(\"load\", function(event) {\n        lazyload(); // By default will lazyload all images with the class .lazyload\n    });\n\n    // Native API - Using Intersection Observer\n    const callback = (entries, observer) => {\n        entries.forEach(entry => {\n            if (entry.isIntersecting) {\n                // An element is intersecting\n                const element = entry.target;\n                element.target.src = yourImageToLoadIn; // Here you add the image src you want to load in\n                observer.unobserve(element); // Stops observing the element\n            }\n        });\n    };\n\n    // Creating an IntersectionObserver instance and passing it in a callback. Options can be added as a second parameter\n    const theObserver = new IntersectionObserver(callback);\n\n    // Start to observe all images with the class .all-images\n    theObserver.observe(document.querySelectorAll('.all-images'));\n</code></pre>\n<p>In this example, the difference in lines of code does seem a lot. However, the advantages of using the Native API's is the because these API's are built-in the browser. jQuery has to be loaded in first excluding the plugins and then be parsed afterward, increasing the load time and data consumption. And because Js is render blocking, the user will be left with an unresponsive screen.</p>\n<blockquote>\n<p>Native code is faster than interpreted / just in time compiled code - Christian “Schepp” Schaefer</p>\n</blockquote>\n<p><em>jQuery is only used as an example to compare with</em></p>\n<h2>Sound great, but how about ...</h2>\n<p>Now comes the question. How is the support, does it support IE version X. Well, some of API's do but not all. The question is whether you need to support IE 6 for example. Does your audience use it? Then maybe this might not be the solution for you as of yet, but you can still use it either way. How? By implementing <strong>Progressive enhancement</strong> and <strong>Feature detection</strong>.</p>\n<p>Progressive enhancement requires you to build up your web project in layers, starting from HTML only to CSS and enhancing it with JS. Your core functionality should be working even if JS is disabled and/or when the CSS does not work for some reason. The user can still make use of the most important features.</p>\n<p>With feature detection, you can make use of the new API's regardless of whether a visitor's browser supports it or not. By implementing the detection you can make use of newer API's for the current browsers and have a fallback for the older browsers. And when the time comes that the API's is supported by everyone then the code will already be up to date.</p>\n<h2>Takeaway</h2>\n<p>All the new technologies are cool and really useful while also increasing productivity. While that is so it is not always necessary for your use-case. Many libraries can already be replaced by the Native API's as of today while supporting older browsers. So why not make use of them?</p>\n<h4>Sources:</h4>\n<ul>\n<li>Talk: Jurgen Tjerik - Progressive enhancement at HvA</li>\n<li>Talk: Christian “Schepp” Schaefer - Breakthrough of native browser API's at NLHTML5 x CSS Day</li>\n<li>The jQuery lazy load snippet:  <a href=\"https://appelsiini.net/projects/lazyload/fadein/\">https://appelsiini.net/projects/lazyload/fadein/</a></li>\n</ul>","frontmatter":{"date":"June 21, 2018","path":"/native-browser-api","title":"Native browser API's"}}},"pathContext":{}}

